const async = require('async');
const each = async.each;
/**
 * 
 */
class StateElement {
    constructor(machine, state, callback) {
        this._machine = machine;
        this._state = state;
        this.callback = callback;
    }
    get machine() {
        return this._machine;
    }
    get state() {
        return this._state;
    }
    execute(iteration) {
        if (this.callback) {
            return this.callback(this, iteration);
        }
    }
}
/**
 * 
 */
class StateMachineCollection {
    constructor(machine) {
        this.machine = machine;
        this._elements = [];
    }
    get elements() {
        return this._elements;
    }
    set elements(s) {
        this._elements = s;
    }
    add(el) {
        var self = this;
        if (el) {
            var toUpdate = [];
            if (!Array.isArray(el)) {
                toUpdate = [el];
            } else {
                toUpdate = el;
            }
            toUpdate.forEach((tu)=>self.elements.push(tu));
        }
        return this;
    }
    clear() {
        this._elements = [];
    }
}
/**
 * 
 */
class EntityElement extends StateElement {
    constructor(machine, state, callback) {
        super(machine, state, callback);
        this.state.children = [];
    }
    get x() {
        return this.state.x;
    }
    get y() {
        return this.state.y;
    }
    get parent() {
        return this.state.parent;
    }
    set parent(p) {
        this.state.parent = p;
    }
    get children() {
        return this.state.children;
    }
}
/**
 * 
 */
class StateRenderer extends StateElement {
    execute(iteration, el) {
        if (this.callback) {
            return this.callback(el, iteration);
        }
    }
}
/**
 * 
 */
class StateOperator extends StateElement {
    execute(iteration, el) {
        if (this.callback) {
            return this.callback(el, iteration);
        }
    }
}
/**
 * 
 */
class StateBuilder extends StateElement {}

const StateMachineBreakException = {};

/**
 * 
 */
class StateMachine {
    constructor() {
        this.remIterations = 0;
        this.iteration = 1;
        this.maxIterations = 10;
        this.async = true;
        this.halt = false;

        this.stateElements = new StateMachineCollection(this);
        this.stateOperators = new StateMachineCollection(this);
        this.stateBuilders = new StateMachineCollection(this);
        this.stateRenderers = new StateMachineCollection(this);

        this._postIteration = null;
    }
    get elements() {
        return this.stateElements;
    }
    get operators() {
        return this.stateOperators;
    }
    get builders() {
        return this.stateBuilders;
    }
    get renderers() {
        return this.stateRenderers;
    }
    get postIteration() {
        return this._postIteration;
    }
    set postIteration(x) {
        this._postIteration = x;
    }
    get async() {
        return this._async;
    }
    set async(x) {
        this._async = x;
    }
    reset(callback) {
        this.stateElements = new StateMachineCollection(this);
        this.iteration = this.remIterations = 0;
        this.halt = false;
        if(callback) callback();
    }
    run(iterations) {
        this.builders.elements[0].execute(1);
        this.advance(iterations);
    }
    stop() {
        this.halt = true;
    }
    advance(iterations) {
        var self = this;
        self.remIterations = iterations;
        var newElements = [];
        var curElements = [];

        var _advance = () => {
            newElements = [];
            curElements = self.elements.elements;
            self.elements.elements = [];

            if (self.remIterations === 0 || self.halt || curElements.length === 0) {
                return;
            }
            var operateOnElement = (stateEl, done) => {
                setTimeout(()=>{
                    var els = stateEl.execute(self.iteration);
                    done(null, els);
                }, 0);  
            };
            var renderElement = (stateEl, done) => {
                setTimeout(()=>{
                    self.renderers.elements[0].execute(self.iteration, stateEl);
                    if(done) done(null);
                },0);
            };
            const processElement = function() {
                if(curElements.length===0) {
                    self.postIteration(self, newElements, self.iteration);
                    self.iteration++;
                    self.remIterations--;
                    setTimeout(_advance, 0);
                }
                const curElement = curElements.pop();
                operateOnElement(curElement, (err, els)=> {
                    renderElement(curElement);
                    newElements = newElements.concat(els);
                    setTimeout(processElement, 0);
                });
            };
            processElement();
        };
        _advance();
    }
}


export { StateElement, StateMachineCollection, StateMachine, EntityElement, StateOperator, StateRenderer, StateBuilder, StateMachineBreakException };